@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bg-home {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100%;

  background-color: #6fb9b0;

  background-image: radial-gradient(
      at 0% 0%,
      rgba(255, 230, 80, 0.8) 0px,
      transparent 50%
    ),
    radial-gradient(at 100% 0%, rgba(255, 255, 255, 0.9) 0px, transparent 50%),
    radial-gradient(at 0% 100%, rgba(255, 250, 180, 0.6) 0px, transparent 50%),
    radial-gradient(at 100% 100%, rgba(80, 160, 150, 0.4) 0px, transparent 50%);

  background-repeat: no-repeat;
  background-size: cover;
}

.bg-end-0 {
  background: linear-gradient(to bottom, #e37124, #f4d2b5);
}

.bg-end-1 {
  background: linear-gradient(to bottom, #429991, #aad2ae);
 
}

.bg-end-2 {
  background: linear-gradient(to bottom, #194399, #36a6df);

}

.bg-end-3 {
  background: linear-gradient(to bottom, #edc21a, #f7f0c4);
}

/* 自訂透明極簡 range */
.range-clean {
  -webkit-appearance: none;
  appearance: none;
  background: transparent; /* 背景透明 */
  width: 100%;
}

/* 軌道（整條線） */
.range-clean::-webkit-slider-runnable-track {
  height: 14px;
  background: rgba(255, 255, 255, 0); /* 白框效果 */
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 1);
}

.range-clean::-moz-range-track {
  height: 14px;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 1);
}

/* ---- FILLED TRACK（滑過的部分）---- */
.range-clean::-webkit-slider-runnable-track {
  background: rgb(255, 255, 255);
}

/* 隱藏原生 thumb（白點） */
.range-clean::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 14px;
  height: 14px;
  background: white;
  border-radius: 50%;
  cursor: pointer;
  margin-top: -1px; /* 調整 thumb 垂直位置 */
}

/* Firefox */
.range-clean::-moz-range-progress {
  background: white; /* 白色進度條 */
  height: 14px;
}

/* Chrome 進度條實作 */
.range-clean::-webkit-slider-runnable-track {
  background: linear-gradient(
    to right,
    white var(--value, 0%),
    transparent var(--value, 0%)
  );
}

/* 去掉 focus 外框 */
.range-clean:focus {
  outline: none;
}

@media (orientation: portrait) {
  .title-portrait {
    margin-top: 100px !important;
  }
}

@media (orientation: landscape) {
  .title-portrait {
    margin-top: 20px !important;
  }
}

@media (orientation: landscape) {
  .quiz-container {
    padding-left: 20px !important;
    padding-right: 20px !important;
  }
}
